
.calendar[data-v-d40a5efa] {
  position: relative;
& >input {
    height: 24px;
    width: 200px;
    font-size: 18px;
}
& .calendar-main {
    position: absolute;
    width: 290px;
    background: #fff;
    box-shadow: 1px 1px 4px #ccc;
& .year-month {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      background: #41a9cc;
      color: white;
      height: 24px;
      padding: 3px 0;
      margin: 2px;
& .year,& .month {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: center;
        position: relative;
& .before {
          display: inline-block;
          position: absolute;
          left: 10px;
}
& .after {
          display: inline-block;
          position: absolute;
          right: 10px;
}
}
}
& .week {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      padding: 4px 0;
& .week-item {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: center;
}
}
& .line {
      margin: 5px;
}
& .day {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
& .day-item {
        text-align: center;
        width: 14.2%;
        display: inline-block;
        padding: 2px 0;
&.selected {
          background: #41a9cc;
          color: white;
}
&.show-only {
          color: #ccc;
}
}
}
& .operator {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin: 5px;
& .bt {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        margin: 0 2px;
        text-align: center;
        color: white;
        padding: 2px;
        background: #41a9cc;
}
}
}
}



