
.select[data-v-bec5517e] {
& .area {
    border: 1px solid #ccc;
    padding: 5px 8px;
    width: 100px;
    display: inline-block;
    position: relative;
&:before {
      content: '';
      position: absolute;
      border: 5px solid #ccc;
      border-color: transparent #ccc #ccc transparent;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      right: 10px;
      top: 8px;
}
&.show {
&:before {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
        top: 13px;
}
}
}
& .select-container {
    border: 1px solid #ccc;
    width: 116px;
    background: #fff;
& .select-item {
      padding: 2px 5px;
&:nth-child(2n+1) {
        background: #f0f0f0;
}
}
}
& .provice {
    position: absolute;
}
& .city,& .county {
    position: absolute;
    left: 116px;
    top: 8px;
}
}



